@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a{color:inherit;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
body{font-family:'Roboto';color:#212529;font-size:18px;line-height:28px}

.container{max-width:860px;margin:0 auto;padding:0 5px}

.loading{position:relative;display:-ms-flexbox;display:flex;padding-top:130px}.loading__label{cursor:pointer;color:#fff;background-color:#4791FF;padding:14px 22px;border-radius:16px;margin-left:10px;-webkit-box-shadow:0px 4px 32px rgba(71,145,255,0.32);box-shadow:0px 4px 32px rgba(71,145,255,0.32);-webkit-transition:-webkit-box-shadow .2s;transition:-webkit-box-shadow .2s;-o-transition:box-shadow .2s;transition:box-shadow .2s;transition:box-shadow .2s, -webkit-box-shadow .2s}.loading__label:hover{-webkit-box-shadow:0px 4px 32px rgba(71,145,255,0.64);box-shadow:0px 4px 32px rgba(71,145,255,0.64)}.loading__upload{display:none}@media (max-width: 425px){.loading{display:block}.loading__label{display:block;margin:10px 0 0}}

.input-field{width:100%;position:relative}.input-field__placeholder{position:absolute;top:50%;left:25px;-webkit-transform:translateY(-50%);transform:translateY(-50%);color:#ACACAC;pointer-events:none;-webkit-transition:all .2s;-o-transition:all .2s;transition:all .2s}.input-field__placeholder_active{top:2px;-webkit-transform:none;transform:none;font-size:14px}.input{display:block;color:#333333;width:100%;padding:22.5px 25px 10.5px;border-radius:16px;font-size:18px;-webkit-box-shadow:0px 4px 64px rgba(0,0,0,0.08);box-shadow:0px 4px 64px rgba(0,0,0,0.08);border:2px solid transparent;-webkit-transition:border .2s;-o-transition:border .2s;transition:border .2s}.input:focus{border:2px solid rgba(71,145,255,0.32);-webkit-box-shadow:0px 4px 64px rgba(71,145,255,0.08);box-shadow:0px 4px 64px rgba(71,145,255,0.08)}

.image-hover{opacity:0;position:absolute;top:0;left:0;bottom:0;right:0;-webkit-transition:opacity .2s;-o-transition:opacity .2s;transition:opacity .2s;z-index:2}.image-hover__icon{position:absolute;cursor:pointer;width:30px;height:30px}.image-hover__close{top:3px;right:3px;fill:#e12c31}.gallery{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:21px -5px 0}.gallery__image-container{padding:5px}.gallery__image-container:hover .image-hover{opacity:1}.empty{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;border:2px dashed #DCDCDC;border-radius:16px;color:#ACACAC;height:450px;margin-bottom:5px}.item-enter{opacity:0}.item-enter-active{opacity:1;-webkit-transition:opacity 300ms ease-in;-o-transition:opacity 300ms ease-in;transition:opacity 300ms ease-in}.item-exit{opacity:1}.item-exit-active{opacity:0;-webkit-transition:opacity 300ms ease-in;-o-transition:opacity 300ms ease-in;transition:opacity 300ms ease-in}

.image{background-position:center;background-size:cover;background-repeat:no-repeat;position:relative;padding-bottom:62%;width:100%;height:100%;-webkit-filter:drop-shadow(0px 4px 32px rgba(0,0,0,0.08));filter:drop-shadow(0px 4px 32px rgba(0,0,0,0.08));border-radius:16px}.image-load{position:absolute;z-index:-1;left:-999999px}

.loader{position:relative;height:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;border:2px dashed #DCDCDC;border-radius:16px;padding:10px;margin:5px}.lds-dual-ring{width:80px;height:80px}.lds-dual-ring:after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid #cef;border-color:#cef transparent #cef transparent;-webkit-animation:lds-dual-ring 1.2s linear infinite;animation:lds-dual-ring 1.2s linear infinite}@-webkit-keyframes lds-dual-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-dual-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.magnify{position:fixed;height:100%;width:100%;top:0;left:0;background:rgba(0,0,0,0.5);z-index:9990;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center}.magnify__image{max-width:860px}

